<template>
  <div class="area-main">
    
    <div class="main">

      <div
        class="tile-blue category-tile"
        v-for="category in categoriesList"
        :key="category.id"
        @click="clickCategoryHandler(category.id)"
      >
        {{ category.name }}
      </div>

      <div
        @click="clickGameHandler(game)"
        class="game-tile"
        v-for="game in gamesList"
        :key="game.id"
      >
        <div
          class="game-name"
          v-if="this.currentSelectGameId !== game.id"
        >
          {{ game.name }}
        </div>
        <div
          class="game-difficult"
          v-if="this.currentSelectGameId === game.id"
        >
          Складність
        </div>
        <div
          class="game-description"
          v-if="this.currentSelectGameId !== game.id"
        >
          {{ game.description }}
        </div>
        <div
          class="game-mode-current"
          v-if="this.getGameModeId(game.id) && this.currentSelectGameId !== game.id"
          @click="this.currentSelectGameId = game.id"
        >
          {{ this.getGameModeId(game.id) }}
        </div>
        <div
          class="game-modes"
          v-if="this.currentSelectGameId === game.id"
        >
          <div
            class="game-mode"
            v-for="modeId in this.getGameModeIds(game.id)"
            :key="modeId"
            :class="{selected: modeId === getGameModeId(game.id)}"
            @click="$event.stopPropagation(); clickGameModeHandler(game.id, modeId)"
          >
            {{ modeId }}
          </div>
        </div>
      </div>
    </div>

    <ControlsPanelBase>
      <template #controls-first>
        <div 
          @click="clickBackHandler"
          class="tile-round tile-blue svg-back">
        </div>
        <CurrentProfile/>
      </template>
    </ControlsPanelBase>
  </div>
</template>

<script>
import { CategoriesList, GamesList } from '@/config/gamesList';
import DomMixin from '@/mixins/domMixin';
import ModeMixin from '@/mixins/modeMixin';
import ControlsPanelBase from '@/components/ControlsPanelBase';
import CurrentProfile from '@/components/CurrentProfile';
export default {
    props: ['categoryId'],
    mixins: [
      DomMixin,
      ModeMixin,
    ],
    created() {
        this.domSetDocumentBackgroundUrl(require('/public/img/bg-empty1-1080p.png'));
    },
    components: {
      ControlsPanelBase,
      CurrentProfile,
    },
    data() {
      return {
        GamesList,
        CategoriesList,
        // categoryId: null,
        currentSelectGameId: null,
      }
    },
    methods: {
      clickCategoryHandler(categoryId) {
          // this.categoryId = categoryId;
          // this.$router.push({ name: 'home'})
          this.$router.push({ name: 'games-list', params: { categoryId: categoryId }})
      },
      clickBackHandler() {
          if (this.categoryId) {
              // this.categoryId = null
              this.currentSelectGameId = null
              this.$router.push({ name: 'games-list' })
          } else {
            this.$router.push({ name: 'home'})
          }
      },
      clickGameHandler(game) {
        if (this.currentSelectGameId !== game.id) {
          this.$router.push({ name: game.route, params: { gameId: game.id, modeId: this.getGameModeId(game.id) } });
        }
      },
      clickGameModeHandler(gameId, modeId) {
        this.setGameModeId(gameId, modeId);
        this.currentSelectGameId = null
      }
    },
    computed: {
      categoriesList() {
        return this.categoryId ?
          [CategoriesList.find(v => v.id === this.categoryId)] : CategoriesList
      },
      gamesList() {
        return this.categoryId ?
          CategoriesList.find(v => v.id === this.categoryId)
            .games
            .map(id => GamesList.find(v => v.id === id)) : []
      },
    },
}
</script>

