import { GamesList, CategoriesList } from "@/config/gamesList";
import Storage from '@/storage';

export default {
    props: ['gameId', 'modeId'],
    data() {
        return {
            gamesMode: {},
        }
    },
    methods: {
        getGame(gameId) {
            return GamesList.find(v => v.id === gameId)
        },
        getGameModes(gameId) {
            return this.getGame(gameId)?.modes || []
        },
        getGameMode(gameId, modeId) {
            return this.getGameModes(gameId)?.find(v => v.id === modeId) || {}
        },
        getGameModeDefault(gameId) {
            return this.getGame(gameId)?.mode || {}
        },
        buildMode() {
            this.mode = {
                ...this.getGameModeDefault(this.gameId),
                ...(this.modeId && this.getGameMode(this.gameId, this.modeId)?.mode || {}),
            }
        },
        getGameModeIds(gameId) {
            return this.getGameModes(gameId).map(v => v.id)
        },
        getGameModeId(gameId) {
            if (this.getGameModes(gameId).length === 0) {
                this.gamesMode[gameId] = null
            }
            if (this.gamesMode[gameId] === undefined) {
                this.gamesMode[gameId] = Storage.getItemValue('gamesModes', gameId) ?? this.getGameModeIds(gameId)[0] ?? null
            }
            return this.gamesMode[gameId];
        },
        setGameModeId(gameId, modeId) {
            if (this.getGameModeId(gameId) !== modeId) {
                this.gamesMode[gameId] = modeId
                Storage.setItemValue('gamesModes', gameId, modeId)
            }
        },
        findCategoryIdByGameId(gameId) {
            return CategoriesList.find(c => !!c.games?.find(g => g === gameId) )?.id
        },
        buildCategoryId() {
            return this.findCategoryIdByGameId(this.gameId)
        },
    },
}