<template>
  <div
    v-if="currentProfile && !gameId"
    :class="`svg-avatar-${this.currentProfile.avatarId}`"
    class="bg-white tile-round"
  />

  <div
    v-if="currentProfile && gameId"
    class="profile-wrapper"
    @click="this.forceUpdate()"
    :key="componentKey"
  >
    <div
      :class="`svg-avatar-${this.currentProfile.avatarId}`"
      class="bg-white tile-round profile-avatar"
    />
    <div class="profile-info">
      <div class="profile-rating">
        <div
          v-for="i in (this.pointsWin % this.pointsExtra)"
          :key="i"
          :class="`add${i}`"
          class="profile-star svg-star1 add"
        />
        <div class="profile-star svg-star1"/>
        {{ profilePoints.win }}
      </div>
      <div class="profile-rating extra">
        <div class="profile-star svg-star2"/>
        <div
          ref="star-extra"
          @transitionend="e => e.target.classList.remove('active')"
          class="profile-star svg-star2 add-extra"
        />
        
        {{ profilePoints.extra }}
      </div>
      <div
        v-if="profilePoints.timer"
        class="profile-rating timer"
      >
        <div class="profile-star svg-stopwatch-color"/>
        {{ profilePoints.timer }}
      </div>
    </div>
  </div>
  
</template>

<script>
  import ProfileMixin from '@/mixins/profileMixin';

  export default {
    props: ['hintFlag', 'resultFlag', 'gameId', 'modeId', 'timerEnableFlag'],
    mixins: [
      ProfileMixin,
    ],
    data() {
      return {
        currentProfile: null,
        profilePoints: {},

        pointsWin: 0,
        pointsExtra: 5,
        
        isHintUsed: false,
        wrongsNumber: 0,

        componentKey: Date.now()
      }
    },
    beforeMount() {
      this.currentProfile = this.getProfile(this.getCurrentProfile())
      this.profilePoints = this.getProfilePoints(this.currentProfile?.id, this.gameId, this.modeId) || {}

      this.emitter.on('onTimerFinish', (e) => {
        if (this.gameId && this.currentProfile) {
          this.addProfilePoints(
            this.currentProfile.id,
            this.gameId,
            this.modeId,
            0,
            0,
            e.points ?? 0
          )
          this.profilePoints = this.getProfilePoints(this.currentProfile.id, this.gameId, this.modeId)
          this.forceUpdate()
        }
      })
    },
    methods: {
      forceUpdate() {
        this.componentKey = Date.now()
      },
    },
    watch: {
      timerEnableFlag(timerEnableFlag) {
        if (this.gameId && this.currentProfile && timerEnableFlag) {
          this.pointsWin = 0
        }
      },
      hintFlag(hintFlag) {
        if (hintFlag === true && this.gameId && this.currentProfile) {
          this.isHintUsed = true
          this.pointsWin = 0
        }
      },
      resultFlag(resultFlag) {        
        if (this.gameId && this.currentProfile) {
          
          if (resultFlag === true) {
            if (this.wrongsNumber === 0 && !this.isHintUsed) {
              this.pointsWin += 1
              const extraPoint = +(this.pointsWin > 0 && this.pointsWin % this.pointsExtra === 0)

              this.addProfilePoints(
                this.currentProfile.id,
                this.gameId,
                this.modeId,
                1,
                extraPoint
              )
              this.profilePoints = this.getProfilePoints(this.currentProfile.id, this.gameId, this.modeId)
              this.forceUpdate()

              if (extraPoint) {
                this.$refs['star-extra'].classList.add('active')
              }
            }

            this.wrongsNumber = 0
            this.isHintUsed = false
          }
          else if (resultFlag === false) {
            this.pointsWin = 0
            this.wrongsNumber += 1
          }
        }
      },
    }
  }
</script>

<style scoped lang="scss">
.profile-wrapper {
  @include box-shadow-mixin;
  @include bg-blue-mixin;
  @include box-radius-white-mixin;
  display: flex;
  border-radius: 4vw;
  max-width: 25vw;
  overflow: hidden;
}


.profile-avatar {
  box-shadow: none;
  border-radius: 4vw;
  margin: -0.2vw;
  min-width: 8vw;
}

.profile-info {
  margin-right: 1vw;
  margin-left: .5vw;
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  height: 8vw;
  writing-mode: vertical-lr;

}

.profile-rating {
  display: flex;
  align-items: center;
  gap: 1vw;
  font-size: 2.5vw;
  font-weight: 800;
  position: relative;
  margin-left: .5vw;
  margin-right: 1vw;
  writing-mode: horizontal-tb;

  &.extra {
    color: $color-yellow-light;
  }

  &.timer {
    gap: .5vw;
    margin-right: 0;
    margin-left: -.5vw;
  }
}

.profile-star {
  display: inline-flex;
  width: 3vw;
  height: 3vw;
  position: relative;
  &.add {
    position: absolute;
    width: 2.5vw;
    height: 2.5vw;
  }

  &.add1 { left: -.5vw; top: -.5vw; }
  &.add2 { left: 1vw; top: -.5vw; }
  &.add3 { left: 1vw; top: 1vw; }
  &.add4 { left: -.5vw; top: 1vw; }

  &.add-extra {
    position: absolute;
    &::before {
      filter: none;
    }

    &.active::before {
      transform: scale(2);
      filter: blur(.5px);
      opacity: 0;
      transition: opacity 1s ease-in, transform 1s ease, filter 1s ease-in;
    }
  }
}

</style>